import React, {useEffect} from 'react'
import { useSelector } from 'react-redux';
import CustomTextField from '../../Layout/CustomTextField';

function AcMembership({onChangeFn}) {
    const secH = useSelector((store) => store.secH);
    const invFormStatus = useSelector((store) => store.invFormStatus)

    useEffect(()=>{
        return ()=>{
            onChangeFn({name:"membership_number", value:""});
            onChangeFn({name:"membership_designation", value:""});
        }
    },[])

  return (
    <div className="sub__form">
        <CustomTextField
            placeholder="Membership No."
            id="membership-no."
            label="Membership No."
            asterisk="*"
            name="membership_number"
            value={secH?.membership_number || ""}
            onChangeFn={onChangeFn}
            formErrors={invFormStatus.errors}
         />

        <CustomTextField
            placeholder="e.g. CPA, IPA, CA"
            id="membership-designation"
            label="Membership Designation"
            asterisk="*"
            name="membership_designation"
            value={secH?.membership_designation || ""}
            onChangeFn={onChangeFn}
            formErrors={invFormStatus.errors}
        />
    </div>
  )
}

export default AcMembership