import React, { useState } from 'react';
import { useSelector } from 'react-redux'; 
import { YES_H, NO_H, ACCOUNTANT_CERTIFICATE_DOCUMENTS } from '../../../../../constants';
import CustomRadioInput from '../../../Layout/CustomRadioInput';
import AcNameEmail from '../AcNameEmail';
// import CustomTextButtonField from '../../../Layout/CustomTextButtonField';
import CustomTextField from '../../../Layout/CustomTextField';
import DocPlaceholder from '../../../Layout/DocPlaceholder';
import { LOADING, SUCCESS, FAILED } from '../../../../../constants';
import { uploadFiles, deleteFile } from '../../../../../actions/Investor-Application-Form/uploadDocActions';
import AcProfBody from '../AcProfBody';
import AcMembership from '../AcMembership';
import { isNumber } from '../../../../../utils/regexFuncs';
import CustomDropdown from '../../../Layout/CustomDropdown';
import { addNewDocToArray, removeDocFromArray, updateDocArray } from '../../../../../utils/documentUploads';
import { useToast } from '../../../../CustomToast';

const QualifiedAccountantCert = ({onFormChange, investorFormId}) => {
    const toast = useToast();
    const secH = useSelector((store) => store.secH);
    const invFormStatus = useSelector((store) => store.invFormStatus)
    
    const [selectedDocType, setSelectedDocType] = useState({label:"Select type of document", value:""})
    const [uploadedDocs, setUploadedDocs] = useState([]);

    const onChangeRadioFn = ({name, value}) =>{ 
      const convrtToNum = parseInt(value);
      onFormChange({name, value:convrtToNum});
    }

    const onChangeFn = ({name, value}) =>{
      if(name === 'phone_number'){
        const isValid = value==='' || isNumber(value);
        if(!isValid) return
    }
      onFormChange({name, value});
    }

    const onChangeDrpDwn = (e) => {
      const {name, value} = e.target;
      const convrtToNum = value && parseInt(value);
      onFormChange({name, value:convrtToNum});
    }

    const onUploadFile = async(file, type={label:'', value:''}) =>{ //call api to upload files
        if(file){                                 
            const fileName = file.name                   
            const formData = new FormData();
            formData.append('file', file);
            formData.append('type', type.value);
            formData.append('section', 'h');
            formData.append('investor_form_id', investorFormId);
            const {success, url, data} = await uploadFiles(formData)
            const extradata = {
                loader: success? SUCCESS : FAILED,
                docUrl: url,
                resData: data
            }
            updateDocArray({setUploadedDocs, fileName, extradata})
        }
    }

    const onAddFiles = (files) => {
        //add selected files(by user) to local state - and call api
        addNewDocToArray({
            files,
            uploadedDocs,
            setUploadedDocs,
            onUploadFile,
            selectedDocType:{...selectedDocType},
        })
    }

    const onClickRemove = (fileName) => {
        //just remove the file from local state
        removeDocFromArray({fileName, uploadedDocs, setUploadedDocs})
    }

    const onClickDel = async({fileName, docData}) => {
      //delete the file from aws & remove the file from local state
      updateDocArray({setUploadedDocs, fileName, extradata: {delLoader: true}})
      const { success } = await deleteFile(docData?.resData);
      success && onClickRemove(fileName);
      success && toast.success("Deleted successfully!");
      success || updateDocArray({setUploadedDocs, fileName, extradata: {delLoader: false}})
      success || toast.error("Please try again!")
  }

    const updateSelectedOption = (option) => {
      //update selected document type to the state
      setSelectedDocType({...option})
    }

  return (
        //  {/* <h4>H.2 Qualified Accountant Certification</h4>
        //  <div className="section__container--box">
        //    <div className="u-padding-y">
        //      <h6>Email to your accountant for electronic signing?</h6>
        //        <div>
        //          <CustomRadioInput */}
    // {/* //               name="should_email_accountant_for_esigning"
    // //               id={`email-yes`}
    // //               content="Yes"
    // //               val={YES_H}
    // //               checked={secH?.should_email_accountant_for_esigning === YES_H}
    // //               onChangeFn={onChangeRadioFn}
    // //             />
    // //             <CustomRadioInput
    // //               name="should_email_accountant_for_esigning"
    // //               id={`email-no`}
    // //               content="No"
    // //               val={NO_H}
    // //               checked={secH?.should_email_accountant_for_esigning === NO_H}
    // //               onChangeFn={onChangeRadioFn}
    // //             />
    // //           </div>

    // //           <div className="u-padding-y">
    // //             { secH?.should_email_accountant_for_esigning === YES_H? <AcNameEmail onChangeFn={onChangeFn} /> : null}
    // //             <CustomTextField 
    // //               type={"form-input-full-width"} 
    // //               label="Address 1"
    // //               asterisk={''}
    // //               id={"address"}
    // //               placeholder="Address"
    // //               name="address_1"
    // //               value={secH?.address_1 || ""}
    // //               onChangeFn={onChangeFn}
    // //               formErrors={invFormStatus.errors}
    // //             />

    // //             <CustomTextField 
    // //               type={"form-input-full-width u-margin-bottom-0"} 
    // //               label="Phone"
    // //               asterisk={''}
    // //               id={"phone"}
    // //               placeholder="Phone Number"
    // //               name="phone_number"
    // //               value={secH?.phone_number || ""}
    // //               onChangeFn={onChangeFn}
    // //               formErrors={invFormStatus.errors}
    // //             />
    // //           </div>

    // //           <div className="u-padding-y">
    // //             {secH?.accountant_name? <AcProfBody onChangeDrpDwn={onChangeDrpDwn}/>: null}

    // //             {secH?.professional_body || secH?.professional_body === 0 ? <AcMembership onChangeFn={onChangeFn}/>: null}
    // //           </div>
    // //       </div>

    // //     </div>

    // //     <hr /> */}

        <div className="section__container--box"> 

        {/* {secH?.should_email_accountant_for_esigning === YES_H ? --Not needed now but later(phase-2)
          <div className="u-padding-y u-margin-bottom-lg">
            <CustomTextButtonField
              id="verify-email"
              placeholder="Enter email address"
              type="email"
              content="Verified" 
              label="Email Verification"
              asterisk={"*"}
              fullWidth={'-full-width'}
            />
          </div>: null} */}

          <h6 className="u-padding-y">Please upload your certificate below. Certificates must not issued more than 2 years before the date of this application and must include the accountant signature, information of the accountant membership of an eligible professional body.</h6>
          <CustomDropdown
              dropdownOptions={ACCOUNTANT_CERTIFICATE_DOCUMENTS}
              selectedOption={selectedDocType}
              updateSelectedOption={updateSelectedOption}
          />
          {selectedDocType?.value ?
              <div className='u-padding-y'>
                <DocPlaceholder onAddFiles={onAddFiles} uploadedDocs={uploadedDocs} onClickRemove={onClickRemove} onClickDel={onClickDel} uniqId="qualifiedaccountantcert_h"/>
              </div>: null
          }
              
          <div className="note__box u-padding-y u-margin-bottom">
            <p><span>Note:</span> For information on this certificate or a list of accountant qualifications, please refer to <a href="https://asic.gov.au/regulatory-resources/financial-services/financial-product-disclosure/certificates-issued-by-a-qualified-accountant/" target="_blank" rel='noreferrer'>https://asic.gov.au/regulatory-resources/financial-services/financial-product-disclosure/certificates-issued-by-a-qualified-accountant/</a></p>
          </div>
        </div>
  )
}

export default QualifiedAccountantCert;