import React, { useState, useEffect, Suspense } from 'react';
import { useSelector } from 'react-redux';
// import InvestorCategory from './SubSections/InvestorCategory';
import QualifiedAccountantCert from './SubSections/QualifiedAccountantCert';
import { I_HAVE_NET_ASSETS_OF_AT_LEAST_AU_H, I_HAVE_EARNED_AT_LEAST_AU_H, I_HAVE_OR_CONTROL_GROSS_ASSETS_OF_AT_LEAST_AU_H } from '../../../../constants';
import { ShimmerCardFormSection, ShimmerCardText } from '../../../stylesheet/Loader';
import { formatAndPrintDocs } from '../../../../utils/validations/helperFunc';

const InvestorCategory = React.lazy(() => import('./SubSections/InvestorCategory'));

const SectionHContent = ({onFormChange, investorFormId, removeDocFromArr}) => {

  const secH = useSelector((store) => store.secH);
  const invFormStatus = useSelector((store) => store.invFormStatus);
  const arrOfVariables =  [I_HAVE_NET_ASSETS_OF_AT_LEAST_AU_H, I_HAVE_EARNED_AT_LEAST_AU_H, I_HAVE_OR_CONTROL_GROSS_ASSETS_OF_AT_LEAST_AU_H]
  const [ isDocVisible, setIsDocVisible ] = useState(false)

  useEffect(() => {
    const isDocVisible=secH?.category_details?.some((each, index) => {
      if(arrOfVariables.includes(each?.category)){
        return true;
      }
    });
    setIsDocVisible(isDocVisible);
  }, [secH]);

  return (
    <div className="section">
      <h2 className="section__heading">Section H: Certificate by Qualified Accountant</h2>
      <ul className="u-margin-bottom-0">
        <li>Chapters 6D or 7 of the Corporations Act 2001.</li>
      </ul>

      <div className="section__container">
        <Suspense fallback={<ShimmerCardText />}>
          <InvestorCategory onFormChange={onFormChange} investorFormId={investorFormId}/>
        </Suspense>

        {isDocVisible? <QualifiedAccountantCert onFormChange={onFormChange} investorFormId={investorFormId}/>: null}
      </div>
      {secH?.documents?.length > 0? formatAndPrintDocs(secH?.documents, removeDocFromArr) : null}

    </div>
  )
}

export default SectionHContent;